$main-bg: #DBCDC6;
$active: #1F1A38;
$block-bg: #7B506F;
.sched-day {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 0.2em solid $block-bg; }
.date-stat {
    background-color: $active;
    color: $main-bg;
    text-align: center;
    width: 5%; }
.units {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 95%; }
